.nav {
  position: absolute;
  width: 100%;
  top: 0;
  left: 0;
}

.nav nav {
  box-shadow: none;
}

.nav ul li {
  color: white;
  cursor: pointer;
}
.nav ul li.login-signup {
  margin-right: 15px;
}
.nav .nav-non-link {
  padding: 0 15px;
}

.nav-non-link:hover {
  border-bottom: 2px solid white;
  background-color: initial;
}

.nav ul li:hover {
  border-bottom: 2px solid white;
  background-color: initial;
}
.nav ul li.login-signup :hover {
  border-bottom: 2px solid white;
  background-color: initial;
}
.logo {
  font-family: "Bebas Neue", cursive;
  font-family: "Kanit", sans-serif;
  font-size: 25px;
  margin-left: 15px;
  font-weight: bold;
}
